@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
} 
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}

.section-map-section {
	p {
		margin: 0;
	}
	.banner-content {
		margin-bottom: 40px;
	}
	.block-item {
		margin: 20px 0;
		h2 {
			line-height: 1.2em;
		}
	}
	.block-store_info {
		.phone-number {
			line-height: 1.2em;
			margin-bottom: 17px;
		}
		.working-day {
			font-size: 13px;
			margin-bottom: 17px;
			color: var(--subtext-color);
			>* {
				line-height: 1.47em;
			}
		}
		.mail-contact {
			margin-bottom: 20px;
		}
	}
	.block-social {
		margin-top: 25px;
		ul {
			margin: 0 -8.5px;
			display: flex;
			flex-wrap: wrap;
			li {
				padding: 0 8.5px;
			}
			a {
				display: flex;
			}
			svg {
				width: 20px;
				height: 20px;
			}
		}
	}
	.field-border {
		border: 0;
		border-bottom: 1px solid var(--miscellaneous-color);
		margin: 20px 0;
	}
	.map-information {
		left: 20px;
		right: 20px;
		top: 20px;
		background: var(--background-color);
		padding: 20px;
		z-index: 11;
		max-width: 375px;
		border-radius: 4px;
	}
	.store-address {
		align-items: center;
		padding: 10px 20px;
		border: 1px solid var(--miscellaneous-color);
		border-radius: var(--border-radius);
		margin-bottom: 22px;
		font-weight: 500;
		>svg {
			@include margin-end(15px);
		}
		.text {
			flex: 1;
			line-height: 1.45em;
		}
	}
	.store-working-day {
		font-size: 13px;
		color: var(--subtext-color);
		>* {
			line-height: 1.47em;
		}
	}
	.mapboxgl-control-container {
		display: none; 
	}
	.mapboxgl-canvas-container {
		display: flex; 
	}
	@media (min-width: 750px) {
		.block-item {
			margin: 30px 0;
			h2 {
				font-size: 45px;
			}
		}
		.field-border {
			margin: 30px 0;
		}
		.block-store_info {
			.phone-number {
				font-size: 25px;
			}
		}
		.mail-contact {
			margin-bottom: 30px;
		}
		.map-information {
			left: 40px;
			right: 40px;
			top: 40px;
			padding: 40px;
		}
	}
	@media (min-width: 1000px) {
		.content-column {
			flex: 0 0 35%;
			max-width: 35%;
		}
		.map-column {
			flex: 0 0 65%;
			max-width: 65%;
		}
		.banner-content {
			max-width: 365px;
		}
	}
}